<template>
  <div>
    <div class="component market-banner py-3 px-4 mb-0 mb-sm-2">

      <div class="market-banner__inner row align-items-center">
        <div class="date market-banner__time col-2">

          <span class="date__time">{{ game.scheduled |formatTime }}</span>
          <br>
          <span class="date__day">{{ game.scheduled |betDate }}</span>
        </div>
        <div class="market-banner__game col-8 col-lg-6 d-flex justify-content-center align-items-center">
          <span class="team text-center">{{ game.home_team }}</span>
          <span class="vs">vs</span>
          <span class="team text-center">{{ game.away_team }}</span>
        </div>
        <div class="col-2">
          <BetButton @click="$router.go(-1)">
            <i class="fa fa-arrow-left"></i>Back
          </BetButton>
        </div>
      </div>
    </div>

    <BetAccordion>
      <b-card no-body class="mb-0 mb-sm-1" v-for="market in filterMarkets(game.markets)" :key="market.id">
        <b-card-header header-tag="header" role="tab" v-b-toggle="`market${market.id}`"
                       class="d-flex justify-content-between align-content-center"
                       :class="{'picked': marketIsSelected(market.id)}">
          <span class="header-anchor">{{ market.name ? market.name : '' }} {{ market.handicap && market.handicap !== '0.00' ? '( ' + market.handicap + ' )' : '' }}</span>
          <span class="circle-arrow"></span>
        </b-card-header>
        <b-collapse :id="`market${market.id}`" :visible="market.name == 'Match Result' ? true : false" accordion="my-accordion" role="tabpanel">
          <b-card-body class="px-4 px-sm-5 pt-4 pb-3">
            <div class="row">
              <div
                v-for="odd in market.odds" :key="odd.id"
                :class="market.odds.length < 3 ? 'col-sm-6' : 'col-md-6 col-lg-4'"
                class="mb-2"
              >
                <a
                  @click="() => { isSelected(game, odd.id) ? removeFromBetSlip(game, odd.id) : addToBetSlip(game,odd,odd.id)}"
                  class="game-id d-flex align-items-center justify-content-between"
                  :class="[isSelected(game, odd.id) ? 'picked' : '', `slip${odd.id}`]"
                >
                  <span class="player">{{odd.name}}</span>
                  <span class="odd">{{odd.value}}</span>
                </a>
              </div>
            </div>
          </b-card-body>
        </b-collapse>
      </b-card>
    </BetAccordion>
    <div class="row">
      <div class="col p-4">
        <a @click.prevent="$router.go(-1)" class="btn btn-primary"> Back </a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import axios from 'axios';
import BetAccordion from "../Utilities/BetAccordion";
import BetButton from "../Utilities/BetButton";
import {GAME_MARKETS_URL} from "../../api_routes";

export default {
  name: "MoreMarkets",
  components: { BetButton, BetAccordion },
  data() {
    return {
      from_path: "",
    };
  },
  mounted() {
    this.from_path = this.$route.params.from_path;
  },
  computed: {
    ...mapGetters({
      game: "gameMarkets",
      betSlip: "betSlip"
    })
  },
  watch: {
    betSlip() {
      this.$forceUpdate();
    }
  },
  methods: {
    ...mapActions(['getMoreMarkets']),
    filterMarkets(markets) {
      let gameMarkets = [];
      markets.filter(m => {
        if (m.id === "385") {
        } else {
          gameMarkets.push(m);
        }
      });
      return gameMarkets;
    },
    addToBetSlip(game, odd, pick) {
      this.isSelected(game, pick);
      this.$store.dispatch("updateBetSlip", { game: game, odd: odd });
      this.$forceUpdate();
    },
    isSelected(game, pick) {
      let isSelected = false;
      if (this.betSlip) {
        this.betSlip.filter(item => {
          if (item.gameId === game.game_id && item.oddId === pick) {
            isSelected = true;
          }
        });
        return isSelected;
      }
    },
    marketIsSelected(marketId) {
      let isSelected = false;
      if (this.betSlip) {
        this.betSlip.filter(item => {
          if (item.market === marketId) {
            isSelected = true;
          }
        });
        return isSelected;
      }
    },
    removeFromBetSlip(game) {
      this.$store.dispatch("removeFromSlip", game);
    },
  }
};
</script>

